#app {
  display: grid;
  grid-template-areas: "aside header" "aside main" "aside footer";
  grid-template-columns: minmax(220px, 300px) auto;
  grid-template-rows: minmax(80px, 90px) auto 70px;
  overflow: hidden;
  max-width: 1920px;
  margin: 0 auto;
}
body,
html {
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  color: #060028;
  background: #08003C;
}
.section-heading {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 42px;
  text-transform: uppercase;
  color: #E1DFEC;
  margin-top: 30px;
  margin-bottom: 20px;
}
.main-buttons {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #FFFFFF;
  padding: 10px 28px;
  background: #EB00FF;
  border-radius: 8px;
}
.main-buttons_big {
  border: none;
  padding: 10px 38px;
  animation: 1.5s linear infinite dlink;
}
.secondary-buttons {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #FFFFFF;
  border: none;
  padding: 10px 38px;
  background: #060026;
  border-radius: 8px;
}
.service-buttons {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  background: #2F0DFF;
  border-radius: 8px;
  padding: 8px 16px;
  border: 1px solid transparent;
  transition: all 0.4s ease-in;
}
.service-buttons:hover,
.service-buttons:focus,
.service-buttons:active {
  background: #2d0dff44;
  border: 1px solid #2d0dff44;
}
.btn-primary {
  color: #FFFFFF;
  font-weight: 500;
  letter-spacing: 0.015em;
  line-height: 106.8%;
  background: #EB00FF;
  border: 1px solid #EB00FF;
  padding: 12px 27px;
  border-radius: 0.25rem;
  border-radius: 8px;
  transition: all 0.4s ease-in;
}
.btn-primary:hover,
.btn-primary:focus,
.btn-primary:active {
  color: #FFFFFF;
  background: #F691FF;
  border: 1px solid #F691FF;
}
.btn-dark {
  font-weight: 500;
  line-height: 106.8%;
  letter-spacing: 0.015em;
  background: #2F0DFF;
  padding: 12px 27px;
  border-radius: 8px;
  border: 1px solid transparent;
  transition: all 0.4s ease-in;
}
.btn-dark:hover,
.btn-dark:focus,
.btn-dark:active {
  background: #2d0dff44;
  border: 1px solid transparent;
}
.btn-warning {
  color: #2F0DFF;
  line-height: 106.8%;
  letter-spacing: 0.015em;
  padding: 12px 27px;
  border-radius: 8px;
  border: 2px solid #2F0DFF;
  transition: all 0.4s ease-in;
  background-color: transparent;
}
.btn-warning:hover,
.btn-warning:active,
.btn-warning:focus {
  color: #fff;
  background-color: #2F0DFF;
  border: 2px solid #2F0DFF;
}
.btn-outline-danger {
  color: #EB00FF;
  font-weight: 500;
  line-height: 106.8%;
  letter-spacing: 0.015em;
  padding: 12px 27px;
  border-radius: 8px;
  border: 2px solid #EB00FF;
  transition: all 0.4s ease-in;
  background-color: transparent;
}
.btn-outline-danger:hover,
.btn-outline-danger:active,
.btn-outline-danger:focus {
  color: #fff;
  background-color: #EB00FF;
  border-color: #EB00FF;
}
::-webkit-scrollbar-thumb {
  width: 8px;
  height: 26px;
  background: #000;
  border-radius: 10px;
  background-color: #060028;
}
::-webkit-scrollbar {
  width: 10px;
  background-color: #f9f9fd8a;
}
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #9d92d71d;
}
.main {
  grid-area: main;
  min-height: 100vh;
  position: relative;
  background: #08003C;
}
.form-label {
  font-size: 0;
}
.br {
  border-right: 1px solid #fff;
}
@media (max-width: 992px) {
  #app {
    grid-template-areas: "header" "main" "footer";
    grid-template-columns: 100%;
    grid-template-rows: auto auto auto;
    position: relative;
  }
  .aside {
    position: absolute;
    top: 74px;
    left: 0;
    padding: 30px 20px 20px;
    z-index: 35;
    transform: translateX(-100%);
    transition: all 0.6s ease-in;
    background: #fff;
  }
  .naw-show {
    transform: translateX(0);
  }
}
.toast-btn {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  opacity: 0.5;
  background-color: transparent;
  border: 0;
}
.toast-text {
  width: 246px;
}
@media (max-width: 368px) {
  .toast-text {
    width: 230px;
  }
}
.logo-link {
  color: #fff;
  text-decoration: none;
  transition: all 0.3s ease-out;
}
.logo-link:hover {
  color: #ffffff58;
  text-decoration: none;
}
.card {
  border: 1px solid rgba(235, 0, 255, 0.75);
}
.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
  border: 1px solid #FFFFFF;
  border-radius: 8px;
  background-color: transparent;
}
.nav-tabs .nav-link:focus,
.nav-tabs .nav-link:hover {
  border: 1px solid #FFFFFF;
  border-radius: 8px;
}
.table td,
.table th {
  border-top: 1px solid transparent;
  vertical-align: middle;
}
.tab-content .tab-pane .table thead th {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.tab-content .table thead th {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-top: none;
}
.tab-content tr:first-child th,
.tab-content tr:first-child td {
  border-top: none;
}
.nav-tabs .nav-item {
  margin-bottom: -2px;
}
.modal-content .table thead th {
  border-bottom: 1px solid transparent;
}
.nav-tabs {
  border-bottom: 1px solid transparent;
}
.nav-tabs:last-child {
  padding-right: 12px;
}
.table {
  color: #fff;
}
.table-wrap {
  padding: 20px 20px;
  margin-top: 10px;
}
@media (max-width: 540px) {
  .table-wrap {
    overflow-x: scroll;
  }
}
.form-control {
  font-size: 1.2 rem;
}
.toast {
  background: #080031;
  border-radius: 11px;
}
.toast-header {
  color: #fff;
  background: #080031;
  border-top-left-radius: 11px;
  border-top-right-radius: 11px;
}
.close {
  color: #f8f9fa;
}
.copyToastBtn {
  color: #EB00FF;
}
.toast-text {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: rgba(255, 255, 255, 0.7);
  background: #080031;
  border: none;
}
.linck {
  font-family: 'DM Sans', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #2F0DFF !important;
  text-decoration: none;
  transition: all 0.2s ease-in;
}
.linck span {
  margin-right: 4px;
}
.linck:hover,
.linck:focus,
.linck:active {
  color: #fff !important;
  text-decoration: none;
}
